.header {
  background-color: #fff;

  $b: &;

  &__top {
    display: flex;
    padding-top: 20px;
    padding-bottom: 33px;

    .logo {
      margin-right: 30px;
    }

    #{$b}__info {
      flex-grow: 1;

      &-top {
        display: flex;
        justify-content: space-between;
        border-bottom: 1px solid #d7d7d7;
        padding-bottom: 7px;

        nav {
          ul {
            display: flex;

            li {
              color: #252525;
              font-size: 12px;

              &:not(:first-child) {
                margin-left: 20px;
              }
            }
          }
        }

        .buttons {
          font-size: 12px;
          color: #252525;

          span {
            margin-left: 10px;
            margin-right: 10px;
          }
        }
      }

      &-bottom {
        display: flex;
        justify-content: space-between;
        padding-top: 20px;

        .image {
          width: 253px;
          height: 70px;
          background-color: #e2e2e2;
        }

        .labels {
          &__item {
            display: flex;
            align-items: center;
            text-decoration: underline;
            color: #252525;
            font-size: 14px;

            &:not(:last-child) {
              margin-bottom: 10px;
            }

            svg {
              margin-right: 10px;
              width: 20px;
              height: 20px;
            }
          }
        }

        .products {
          display: flex;

          &__item {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 70px;
            height: 70px;
            border: 1px solid #d9d9d9;

            &:not(:first-child) {
              margin-left: 20px;
            }
          }
        }

        .contacts {
          .working-time {
            display: flex;
            justify-content: space-between;
            align-items: center;

            span {
              font-family: Roboto;
              font-size: 12px;

              &:first-child {
                font-weight: 700;
              }
            }
          }

          .phone {
            display: inline-block;
            font-weight: 700;
            font-size: 25px;
            margin-top: 20px;
          }

          .place {
            display: flex;
            justify-content: space-between;
            padding-top: 6px;
            border-top: 1px solid #d7d7d7;

            &__city {
              font-size: 12px;
            }

            &__change {
              display: flex;
              align-items: center;
              font-size: 12px;

              svg {
                width: 10px;
                height: 10px;
              }

              span {
                border-bottom: 1px dotted;
              }
            }
          }
        }
      }
    }
  }

  &__bottom {
    display: flex;
    justify-content: space-between;
    overflow: hidden;
    background-color: #000;
    border-radius: 15px;
    overflow: hidden;
    border: 0 solid transparent;
    box-shadow: 0 2px 3px rgba(0, 0, 0, 0.25);

    .search,
    .call-to-action {
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
      background-color: #ffcc00;
      text-transform: uppercase;
      font-weight: 700;
      font-size: 14px;
      padding: 10px 15px;

      svg {
        width: 19px;
        height: 19px;
        fill: #000;
      }
    }

    nav {
      ul {
        display: flex;
        align-items: center;
        font-weight: 700;
        color: #fff;
        font-size: 14px;
        margin: 0;
        height: 100%;

        li {
          display: flex;
          align-items: center;
          height: 100%;

          a {
            display: flex;
            align-items: center;
            height: 100%;
            padding: 10px 15px;
          }

          &:hover,
          &.is-active {
            color: #333;
            background-color: #ffcc00;
          }
        }
      }
    }
  }
}
